import React, { Component } from 'react';
import './normalize.css';
import './App.css';
import LogoFile from './fabafLogo.png';
import DataFile from './data.json';
import CV from './Greg_Carvalhal_CV_Frontend_Developer_2019.pdf';


/**
 * Main content
 * 
 */
const SectionContent = (props) => {
  return (
    <section className="content container">
      <div className="content-inner">
        <ContentHeader data={props.data}/>
        <ContentMain data={props.data}/>
      </div>
    </section>
  );
};

const ContentHeader = (props) => {
  return (
      <header className="header-main">
        <h1><Logo /> <span className="text">{props.data.name.first} {props.data.name.last}</span></h1>
        <h2>{props.data.position}</h2>
        <p><a rel="noreferrer noopener" target="_blank" href={CV}>Download CV</a></p>
      </header>
  );
};

const Logo = (props) => {
  return (
    <span className="logo">
      <img src={LogoFile} alt="Greg Carvalhal FABAF logo"/>
    </span>
  );
};

const ContentMain = (props) => {
  return(
    <main>
        <ContentMainSummary summary={props.data.summary} />
        <ContentMainExperience experience={props.data.experience}/>
        <ContentMainEducation />
    </main>
  );
};

const ContentMainSummary = (props) => {
  return(
    <div className="summary section">
    <h2>Summary</h2>
    <p>{props.summary}</p>
    </div>
  );
};

const ExperiencePositions = (props) => {
  const company = props.company;
  const positions = company.positions;
  
  return(
  <div className="row company-wrapper">
    <h3 className="company">{company.company}, {company.location}</h3>
    {positions.map((position, index) =>
      <div key={index} className="position card">
        <div className="card-content">
        <h4><span className="period">{position.period}</span> <strong className="pos-name">{position.position}</strong> {(position.years == null ? "" :  <span className="years">{position.years}</span>)}</h4>
        <ul className="responsibilities">
        {position.responsibilities.map( (resp, index) => 
          <li key={index}>{resp}</li>
         )}
         </ul>
         </div>
      </div>
    )}
  </div>
  );
};

const ContentMainExperience = (props) => {
  return(
    <div className="experience section">
      <h2>Experience</h2>
        <div className="experience-wrapper">
          {props.experience.map((company, index) =>
            <ExperiencePositions key={index} company={company}/>
          )}
        </div>
    </div>
  );
};

const ContentMainEducation = (props) => {
  return(
    <div className="education section">
    </div>
  );
};


/**
 * Sidebar
 *  
 */
 const SectionSidebar = (props) => {
  return (
    <section className="sidebar">
      <SidebarHeader contact={props.data.contact}/>
      <SidebarMain data={props.data}/>
    </section>
  );
};

const SidebarHeader = (props) => {
  return (
    <header className="header-side">
      <h1>Contact</h1>
      <p className="contact-list">
      <span><span className="label">Location:</span> <span className="value">{props.contact.Location}</span></span>
      {props.contact.social.map((value, index) => 
      <span key={index} className="social-link"><span className="label">{value.site}:</span> <a className="value" title={value.site} href={value.url}>{value.url}</a></span>
      )}
      </p>
    </header>
  );
};

const SidebarMain = (props) => {
  return (
    <main>
      <h2>Top Skills</h2>
      <SkillsList skills={props.data.skills}/>
      <h2>Tools</h2>
      <ToolsList tools={props.data.tools} />
      <h2>Strengths</h2>
      <StrengthsList strengths={props.data.strengths} />
      <h2>Languages</h2>
      <LanguageList languages={props.data.languages}/>
    </main>
  );
};

const SkillsList = (props) => {
  return (
    <ul className="skills-list inlined-list">
      {props.skills.map((value, index) => 
        <li key={index}>
          <span className="skill-name">{value}</span>
          </li>
        )}
    </ul>
  );
};

const ToolsList = (props) => {
  return(
    <ul className="tools-list inlined-list">
      {props.tools.map((value, index) =>
          <li key={index}>
            <span className="tool-name">{value}</span>
          </li>
        )}
    </ul>
  );
};

const StrengthsList = (props) => {
  return(
    <ul className="strengths-list inlined-list">
      {props.strengths.map((value, index) =>
          <li key={index}>
            <span className="strength-name">{value}</span>
          </li>
        )}
    </ul>
  );
};

const LanguageList = (props) => {
  return (
    <ul className="languages-list">
      {
        props.languages.map((value, index) => 
        <li key={index}>
          <span className="lang-name">{value.language}</span>
          <span className="lang-prof">{value.proficiency}</span>
          </li>
        )}
    </ul>
  );
};


/**
 * App with render
 */
class AppRoot extends Component {
  constructor(props) {
    super(props);

    this.state = {
      DataFile,
    };
  }
  
  render() {
    return (
      <div className="approot">
        <SectionContent data={DataFile.data} />
        <SectionSidebar data={DataFile.data} />
      </div>
    );
  }
};

export default AppRoot;